import React from 'react'


class Home extends React.Component {
    constructor() {
        super()
        this.state = {
            pallete : {
                light: '#CCCCCC',
                main: '#808080',
                dark: '#666666',
                contrastText: '#ffffff'
              },
        }

        //this.handleClick = this.handleClick.bind(this)
    }

    //handleClick(event) {}
    

    render() {
        return (
            <>
                <h1 style={{fontFamily: 'Merienda One', color: this.state.pallete.contrastText}}>
                    Home
                </h1>
            </>
            
            
            
        );
    }
}

export default Home